.close-modal,
.close-ico {
  position: absolute;
  top: 0;
  right: 0;
  padding: 30px;
  z-index: 999;
  cursor: pointer;
}

.close-modal:after,
.close-ico:after,
.close-modal:before,
.close-ico:before {
  content: "";
  position: absolute;
  border: 1px solid #cfcfcf;
  height: 40px;
  top: 10px;
  right: 30px;
}

.close-modal:after,
.close-ico:after {
  transform: rotate(45deg);
}

.close-modal:before,
.close-ico:before {
  transform: rotate(-45deg);
}
